.loader-wrapper {
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.delete-btn-wrapper {
  display: flex;
  justify-content: flex-end;
}

.delete-ass-btn {
  width: 135px;
  text-align: center;
  padding: 5px 3px;
  border-radius: 5px;
  color: #f07371;
  font-family: "Roboto", sans-serif;
  text-decoration: underline;
  font-weight: bold;
  transition: all 0.2s ease-out;
}

.delete-ass-btn:hover {
  background-color: #ff0000;
  color: #fff;
  opacity: 0.8;
  text-decoration: none;
}

.ass-title {
  margin-top: 1.5%;
  padding: 5px 15px;
  border-radius: 5px;
  transition: all 0.1s ease-out;
}

.TeacherAss::-webkit-scrollbar {
  width: 20px;
}

.TeacherAss::-webkit-scrollbar-track {
  background-color: transparent;
}

.TeacherAss::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

.TeacherAss::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}

.assListItem {
  height: 14vh;
  border-radius: 25px;
  border-style: solid;
  border-color: #7398ff;
  text-align: left;
  list-style-type: none;
  font-size: 15px;
  margin: 10px;
  white-space: pre-line;
  font-weight: bold;
}

.assListItem p {
  text-align: left;
  padding-right: 15px;
  font-weight: normal;
  font-size: small;
}

.assListItem span1 {
  float: right;
  padding-right: 11px;
  font-weight: bold;
  font-size: small;
  cursor: pointer;
}

.assListItem span2 {
  float: right;
  font-weight: normal;
  font-size: small;
  cursor: pointer;
}

.ass-span {
  float: right;
  padding-right: 22px;
  font-weight: bold;
  font-size: small;
}

.assignment-container {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 800px) {
  .assignment-list-wrapper {
    height: 530px;
    overflow-y: scroll;
  }
}

@media screen and (min-width: 1450px) {
  .assignment-list-wrapper {
    height: 540px;
    overflow-y: scroll;
  }
}

.no-assigment-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.no-assignment-img {
  height: 350px;
  width: 400px;
}

.create-class-container {
  display: flex;
  justify-content: center;
}

.create-assignment-btn {
  width: 320px;
  height: 50px;
  background-color: #28A745;
  border-radius: 5px;
  color: white;

  font-family: Helvetica, sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.5px;
}
.create-assignment-btn:hover {
  background-color: #027A48;
}
.create-assignment-btn:active{
  background-color: #05603a;
}
