#confirmAndCancelButtons{
  margin-bottom: 12%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 72%;
}

#createTeamButton {
  display: flex;
  justify-content: center;
  margin-top: 6%;
}

#createTeamCancelButton{
  width: 47%;
  height: 60px;
  background-color: #E4E7EC;
  border-radius: 5px;
  color: #c60d00;
  border: solid 1px #C60D00;

  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
}

#createTeamConfirmButton{
  width: 47%;
  height: 60px;
  background-color: #28a745;
  border-radius: 5px;
  color: white;

  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
}

#createTeamTitle{
  margin-top: 10%;
  margin-bottom: 2%;
}

#joinTeamButton{
  width: 118px;
  height: 34px;
  background-color: #4a7dfc;
  border-radius: 5px;
  color: white;
  border: none;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  margin-top: 4%;
}

#joinTeamButton:hover {
  background-color: #1d2ff9;
}

#joinTeamButton:active {
  background-color: #102080;
}

.members-and-join-button-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 240px;
}

.members-count{
  margin-top: 15px;
  margin-bottom: 5px;
}

.members-count-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

#modal{
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  width: 100vw;
  height: 100vh;
}

#modalContent{
  margin-top: 8%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 43%;
  height: 60%;
}

#teamInputField{
  border-radius: 5px;
  margin-top: 10px;
  height: 50px;
  width: 100%;
}

#teamFieldContainer{
  display: flex;
  flex-direction: column;
  width: 72%;
}

#teamList {
  width: 100%;
}

.team-tile{
  border-radius: 5px;
  background-color: #4a7dfc;
  margin-bottom: 2%;
  margin-left: 3%;
  margin-right: 3%;
}

.team-tile-content{
  padding: 1% 4%;
  background-color: white;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border: 1px solid #212529;
  border-top: none;
  user-select: none;
}

.team-tile-info{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}

.team-tile-title{
  display: flex;
  justify-content: left;
  margin-left: 4%;
  padding: 5px 0;
  user-select: none;
}

#teamTitle {
  margin-left: 3%;
  margin-bottom: 4%;
}


